.modal-overlay {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.7);
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	width: 100%;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease-in-out;
}

.modal {
	position: absolute;
	top: 40px;
	width: 500px;
	padding: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	display: none;
	border-radius: 30px;
	background-image: url("../img/bg-form.jpg");
	background-size: cover;
}

.modal-overlay--visible {
	opacity: 1;
	z-index: 50;
	visibility: visible;
	transition: all 0.3s ease-in-out;
}

.modal--visible {
	display: block;
}
.form-modal__title {
	margin-top: 10px;
	text-align: center;
	& span {
		margin: 5px 0 20px;
		display: block;
		font-family: Montserrat;
		font-size: 18px;
		font-weight: 300;
	}
}

.form-modal__form {
	display: flex;
	flex-direction: column;
	row-gap: 30px;
}

.form-modal__label {
	align-self: center;
	max-width: 380px;
	width: 100%;
}

.form-modal__label input {
	display: inline;
}

.form-modal__label input {
	width: 100%;
	height: 20px;
	padding: 30px 0 30px 20px;
	color: #000;
	border-radius: 15px;
	border: 1px solid #fff;
	background-color: transparent;
	font-size: 17px;
	font-weight: 500;
}


.form-modal__btn {
	align-self: center;
	max-width: 380px;
	width: 100%;
	height: 70px;
	border-radius: 15px;
	font-size: 18px;
	font-weight: 700;
}


.form-modal__label {
	display: flex;
	flex-direction: column-reverse;
}

.just-validate-error-label {
	margin-bottom: 6px;
	font-size: 18px;
}