.client {
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		gap: 30px;
		margin: 40px 0;
		padding: 30px;
		border-radius: 10px;
		background: #282828;
	}
	&__item {
		flex: 0 0 140px;
		align-self: center;
		position: relative;
		height: 96px;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
			scale: 1;
			margin: auto;
		}
		& img:hover {
			transition: scale 1s ease-in-out;
			scale: 1.2;
		}
	}
}
