.work__wrap {
	padding-left: calc((100% - 1180px) / 2);
	margin-bottom: 120px;
}

.work__slider {
	width: 1970px;
	border-radius: 30px;
	overflow: hidden;
}

.work__slider-wrapper {
	width: 100%;
	display: flex;
}

.work__slid {
	flex-shrink: 0;
	cursor: grab;
}
.work__slid:active{
	cursor: grabbing;
}

.work__slid {
	position: relative;
	width: 100%;
	height: 409px;
	border-radius: 30px;
	overflow: hidden;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.work-slider-buttons {
	position: absolute;
	left: 58%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	column-gap: 130px;
	padding: 0 20px;
	height: 9px;
	margin-top: 20px;
}
.work-slider-pagination {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	column-gap: 14px;
	&::after {
		content: "";
		position: absolute;
		right: 65px;
		top: 50%;
		transform: translateY(-50%);
		width: 90px;
		height: 2px;
		background-color: #33c2ff;
	}
}

.work-slider-button-next {
	flex: 0 0 24px;
	position: static;
	margin-top: 0;
	width: 24px;
	height: 9px;
	background-image: url("../img/icons/arrow-next.svg");
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
	cursor: pointer;
}

.work {
	margin-top: 100px;
	&__wrap {
		margin-top: 50px;
	}
	&__video {
		margin-bottom: 40px;
	}
	&__btn {
		text-align: center;
	}
}

@media (min-width: 641px) {
	.work {
		&__video {
			display: flex;
			column-gap: 10px;
		}
	}
}

@import "video/_video.scss";

@media (max-width: 961px) {
	.work__slid {
		
		height: 300px;
		
	}
}

@media (max-width: 1282px) {
	.work__wrap {
		max-width: 1210px;
		padding: 0px 15px;
	}
	.work__slider {
		width: 100%;
	}

	.work-slider-buttons {
		position: absolute;
		right: 30px;
		left: 0;
	}
}
@media (max-width: 641px) {
	.work-slider-buttons {
		column-gap: 60px;
	}
	.work-slider-pagination {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		column-gap: 14px;
		&::after {
			right: 60px;
			width: 30px;
		}
	}
}