.reviews {
	position: relative;
	padding: 75px 0 75px 15px;
	&::after {
		content: "";
		position: absolute;
		z-index: -1;
		left: 0;
		bottom: -210px;
		width: 100%;
		height: 570px;
		background-image: url(../img/bg.png);
		background-position: center;
		background-repeat: repeat-x;
		background-size: contain;
	}
	& h4 {
		margin-bottom: 10px;
	}
	&__wrap {
		padding-left: calc((100% - 1180px) / 2);
	}
}

.reviews-slider {
	width: 1780px;

	padding-top: 50px;
	overflow: hidden;
}

.reviews-slider__wrap {
	display: flex;
	width: 100%;
}

.slid-reviews {
	flex-shrink: 0;
	cursor: grab;
}

.slid-reviews:active {
	cursor: grabbing;
}

.reviews-slider-buttons {
	position: absolute;
	left: 58%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	column-gap: 130px;
	padding: 0 20px;
	height: 9px;
	margin-top: 20px;
}
.reviews-slider-pagination {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	column-gap: 14px;
	&::after {
		content: "";
		position: absolute;
		right: 65px;
		top: 50%;
		transform: translateY(-50%);
		width: 90px;
		height: 2px;
		background-color: #33c2ff;
	}
}

.reviews-slider-button-next {
	flex: 0 0 24px;
	position: static;
	margin-top: 0;
	width: 24px;
	height: 9px;
	background-image: url("../img/icons/arrow-next.svg");
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
	cursor: pointer;
}

@media (max-width: 1282px) {
	.reviews {
		max-width: 1210px;
		padding: 50px 15px;
	}
	.reviews-slider {
		width: 100%;
	}

	.reviews-slider-buttons {
		position: absolute;
		right: 30px;
		left: 0;
	}
}

@media (max-width: 641px) {
	.reviews-slider-buttons {
		column-gap: 60px;
	}
	.reviews-slider-pagination {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		column-gap: 14px;
		&::after {
			right: 60px;
			width: 30px;
		}
	}
}

@import "_slider-reviews.scss";
