.slider-section {
	position: relative;
	padding-left: calc((100% - 1500px) / 2);
}

.slider {
	position: relative;
	width: 2300px;
	height: 500px;
	overflow: hidden;
	&::after {
		content: "";
		position: absolute;
		right: 0;
		top: 0;
		height: 100%;
		width: 50%;
		background: rgb(22, 22, 22);
		background: linear-gradient(90deg, rgba(22, 22, 22, 0) 0%, rgba(22, 22, 22, 1) 100%);
		pointer-events: none;
	}
}

.slider__wrapper {
	display: flex;
	width: 100%;
	height: 100%;
}

.slid {
	position: relative;
	flex-shrink: 0;
	border-radius: 30px;
	overflow: hidden;
	cursor: grab;
}

.slid:active {
	cursor: grabbing;
}
.slid__img {
	position: relative;
	height: 100%;
	border-radius: 30px;
	overflow: hidden;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		max-width: 900px;
		width: 100%;
		height: 100%;
		border-radius: 30px;
		filter: brightness(0.45);
		object-fit: cover;
	}
}

.slid__text {
	position: absolute;
	right: -80px;
	top: 50%;
	transform: translateY(-50%);
	width: 510px;
}

.slid__title {
	color: #fff;
	font-family: "Bebas-Neue";
	font-size: 96px;
	line-height: 1;
}

.slid__descr {
	display: block;
	max-width: 400px;
	text-align: justify;
	font-size: 16px;
	font-style: normal;
	font-weight: 300;
	line-height: normal;
}

.slid__btn {
	display: block;
	margin-top: 35px;
	padding: 15px 35px;
	color: #fff;
	font-size: 16px;
	font-style: normal;
	font-weight: 600;
	border-radius: 10px;
	background: linear-gradient(rgb(0, 87, 255) 0px, rgb(33, 130, 244) 100%);
	transition: all 0.3s ease-in;
	&:hover {
		background: linear-gradient(rgb(33, 130, 244) 100%, rgb(0, 87, 255) 0px);
	}
}

.swiper-slide-next > .slid__text {
	opacity: 0;
}

.swiper-button-next::after {
	display: none;
}

.slider-buttons {
	position: absolute;
	left: 60%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	column-gap: 130px;
	padding: 0 20px;
	height: 9px;
	margin-top: 20px;
}
.swiper-pagination {
	position: static;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	column-gap: 14px;
	&::after {
		content: "";
		position: absolute;
		right: -105px;
		top: 50%;
		transform: translateY(-50%);
		width: 90px;
		height: 2px;
		background-color: #33c2ff;
	}
}

.swiper-pagination-bullet {
	width: 10px;
	height: 10px;
	opacity: 1;
	background-color: #707070;
	transition: all 0.3s ease-in-out;
}

.swiper-pagination-bullet-active {
	width: 24px;
	height: 24px;
	background-color: transparent;
	border: solid 3px #33c2ff;
}

.swiper-button-next {
	flex: 0 0 24px;
	position: static;
	margin-top: 0;
	width: 24px;
	height: 9px;
	background-image: url("../img/icons/arrow-next.svg");
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
}
