@media (max-width: 1281px) {
	.slider-section {
		width: 100%;
		margin: 0 auto;
	}

	.slider {
		width: 100%;
		height: 450px;
		&::after {
			display: none;
		}
	}

	.slid__img {
		& img {
			max-width: 850px;
		}
	}

	.slid__text {
		right: -5%;
		bottom: 50px;
	}

	.slid__title {
		font-size: 50px;
	}

	.slid__descr {
		font-size: 14px;
	}
	.slider-buttons {
		position: absolute;
		right: 30px;
		left: 0;
	}
}

@media (max-width: 641px) {
	.slid__btn {
		display: none;
	}
	.slid__text {
		width: 100%;
		bottom: 200px;
	}
	.slid__descr {
		display: none;
	}

	.slider-buttons {
		column-gap: 60px;
	}
	.swiper-pagination {
		&::after {
			right: -46px;
			width: 35px;
		}
	}
}
